/* Global */
@import url('https://fonts.googleapis.com/css?family=IBM+Plex+Sans&display=swap');
body{
  font-family: 'IBM Plex Sans', sans-serif;
}
.home-logo{
  width: 100px;
  margin-bottom: 0.4rem;
}
h1{
  font-size: 2rem;
  font-weight: 400;
  color: #515067;
}
h2{
  font-size: 1.6rem;
  font-weight: 400;
  color: #515067;
}
h3{
  font-size: 1.4rem;
  font-weight: 400;
  color: #515067;
}
h4{
  font-size: 1rem;
  font-weight: 400;
  color: #515067;
}
a{
  color: #202B2F;
  text-decoration: none;
  transition: all 0.6s ease;
}
a:hover{
  color: #F46993;
  text-decoration: none;
}
.section{
  padding-top: 6rem;
  padding-bottom: 6rem;
}
.mt-tiny{
  margin-top: 0.5rem;
}
.mt-small{
  margin-top: 1rem;
}
.mt-medium{
  margin-top: 3rem;
}
.label-dim{
  color: #B6C9D3;
}
.label-dim.small{
  font-size: 0.6rem;
}
.bg-dark{
  background: linear-gradient(#647D88, #202B2F);
}
.bg-dark .card-title{
  color: #fff;
}
.icon{
  width: 24px;
}
.icon.icon-arrow{
  width: 70px;
}
.container{
  width: 90%;
}
.search-container{
  transition: all 0.6s ease;
}
.search-container:hover{
  color: #F46993;
  transform: translatex(-8px);
  transform-origin: bottom left;
  text-shadow: 0 12px 10px #c3d6df;
}
.section-more{
  transition: all 0.6s ease;
}
.section-more:hover .icon{
  transform: translateY(4px);
}
.social-icons svg.icon{
  padding-left: 0.7rem;
  opacity: 0.7;
  width: 26px;
  height: 26px;
  transition: all 0.6s ease;
}
.social-icons svg.icon:hover path, .social-icons svg.icon:hover rect{
  fill: #F46993;
  transition: all 0.3s ease;
}
.section-header{
  margin-top: 5rem;
}
/* Topbar */
.topbar{
  padding-top: 2rem;
  padding-bottom: 2rem;
  color: #707070;
  font-size: 0.9rem;
  width: 100%;
}


/* Home */
.home{
  background: linear-gradient(#ffffff,#D5E5ED);
  height: 100vh;
  width: 100vw;
  overflow: scroll;
}

/* Navigation */
.main-navigation{
  /* color: #707070; */
  padding-top: 12rem;
  font-size: 0.9rem;
  z-index: 1000;
}
.main-navigation a{
  text-decoration: none;
  padding: 0.4rem 1rem 0.4rem 0;
  color: #707070;
  /* color: #8BACBE; */
  transition: all 0.6s ease;
  display: inline-block;
}
.main-navigation a:hover{
  color: #F46993;
  transform: translateX(8px);
  transform-origin: bottom left;
  text-shadow: 0 12px 10px #c3d6df;
}
.main-navigation ul{
  margin-left: 0;
  padding-left: 0;
}
.main-navigation ul li{
  list-style-type: none;
}
.secondary-nav{
  right: 0;
}
.intro{
  padding-top: 6rem;
  text-align: left;
}
.intro-text{
  margin: auto;
  color: #707070;
}
.showmemodal {
  position: absolute;
  background: #f4f4f4;
  left: 280px;
  top: 150px;
  padding: 16px;
}
.contact-cta-container{
  margin-top: 2rem;
}
.contact-cta .icon{
  width: 20px;
  margin-right:8px;
}
button.contact-cta{
   background-color: #F46993;
   border: none;
   border-radius: 50px;
   color: #fff;
   font-size: 1rem;
   padding: 12px 48px;
   transition: all 0.6s ease;
}
button.contact-cta:hover{
  transform: translateY(-8px);
  box-shadow: 0 6px 10px #f5f5f5;
  background-color: #fff;
  color: #F46993;
}

/* Featured */
.featured{
  display: block;
  padding-top: 4rem;
  width: 100%;
}
.featured .label-dim{
  padding: 2rem;
}

/* Featuredcard */
.featuredcard{
  background-color: #515067;
  border-radius: 2px;
  width: 100%;
  padding-top: 140%;
  transition: all 0.6s ease;
  margin-bottom: 1.4rem;
  background-size: cover;
  background-repeat: no-repeat;
}
.featuredcard.smallcard{
  padding-top:90%;
}
.featuredcard:hover{
  transform: translateY(-16px);
  box-shadow: 0 6px 10px #A5BAC4;
}

/* Resourcedcard */
.resourcecard{
  background-color: #fff;
  border-radius: 2px;
  width: 100%;
  padding-top: 100%;
  transition: all 0.6s ease;
  margin-bottom: 1rem;
}
.resourcecard:hover{
  transform: translateY(-16px);
  box-shadow: 0 6px 10px #303f44;
}
/* Blog */

.blog-card .excerpt{
  color:#7e7e7e;
  font-size: 0.9rem;
}
/* Featuredblog */
.featuredblog{
  background-color: #fff;
  border-radius: 2px;
  width: 100%;
  padding-top: 60%;
  transition: all 0.6s ease;
  margin-bottom: 1rem;
}
.blog-card:hover .featuredblog{
  transform: translateY(-16px);
  box-shadow: 0 6px 10px #dee8ec;
}
.blog-arrow{
  vertical-align: middle;
  transition: all 0.6s ease;
}
.blog-arrow:hover{
  transform: translateX(16px);
}
/* Masonry */
.my-masonry-grid {
  display: -webkit-box; /* Not needed if autoprefixing */
  display: -ms-flexbox; /* Not needed if autoprefixing */
  display: flex;
  margin-left: -30px; /* gutter size offset */
  width: auto;
}
.my-masonry-grid_column {
  padding-left: 30px; /* gutter size */
  background-clip: padding-box;
}

/* Sectionheading */
.sectionheading{

}
.bg-dark .sectionheading h3{
  color: #fff;
}
/* Contact */
.contact-subtext{
  text-align: center;
}
.contact .section-title{
  text-align: center;
}
.contact form{
  text-align: center;
  padding-top: 2rem;
}
.contact form input{
  background-color: transparent;
  border: 1px solid #647D88;
  height: 48px;
  width: 49%;
  float: left;
  padding: 0 0.8rem;
}
.contact form input:nth-child(2){
  float: right;
}
.contact form textarea{
  background-color: transparent;
  border: 1px solid #647D88;
  margin-top: 1rem;
  width: 100%;
  padding: 0.8rem;
}
.contact form button{
  margin-top: 1.8rem;
}

/* Footer */
footer{
  padding: 3rem 0 1rem 0;
}

/* About */
.featured-img-container{
  height: 380px;
  overflow: hidden;
}
.featured-img{
  width: 100%;
}
/* Media Query */
@media (min-width: 1400px){
  .container {
      max-width: 80%;
  }
}
@media (min-width: 1200px){
  .container {
      max-width: 1160px;
  }
}
@media (min-width: 768px){
  .intro{
    text-align: center;
  }
  .intro-text{
    max-width: 420px;
  }
}
@media (min-width: 576px){
  .container{
    width: 100%;
  }
}
